:root {
  --bg-color: #030807;
  --alt-bg-color: #e4e4e4;
  --blackish-type: #272727;

  --nav-height: 52px;
}

/* ScrollMagic... magic */
body, html, .scrollContainer, #content-wrapper {
  height: 100%;
  background-color: var(--alt-bg-color);
  /*nasty
  height: calc(100vh - 70px);
  /**/
}

.scrollContainer {
  overflow-y: scroll;
  overflow-x: hidden;
  width: 100%;

}

/* global style */

body {
  background-color: var(--bg-color);
  color: white;
  font-family: 'Gotham Book';
  font-size: 16px;
  /*nasty
  padding-top: 70px;
  /**/
}

.section {
	background-color: var(--bg-color);
	width: 100%;
	/*min-height: 100vh;*/
	/*nasty
	height: calc(100vh - 70px);
	/**/
}



.nav {
	/*nasty*/
	position: fixed;
	top: calc(-1 * var(--nav-height));
	/**/
	width: 100%;
	height: var(--nav-height);
	background-color: var(--alt-bg-color);
	z-index: 9999;
	font-family: "Gotham Condensed";
	font-size: 24px;
	transition: top 0.25s;
}

.nav.show {
	top: 0;
	transition: top 0.25s;
}

.nav .logo {
	margin-left: 14px;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.nav-holder {
	position: absolute;
	right: 22px;
	top: 0;
	width: 100%;
	height: 100%;
	/*max-width: 590px;*/
	/*max-width: 500px;*/
	max-width: 500px;
}

.fr .nav-holder {
	/*max-width: 680px;*/
	/*max-width: 600px;*/
	max-width: 560px
}

.nav-menu {
	position: relative;
	top: 50%;
	transform: translateY(-50%);

    display: flex;
    justify-content: space-between;
}

.nav-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.nav-menu a, .nav-menu a:link, .nav-menu a:active {
	text-decoration: none;
    cursor: pointer;
    color: var(--blackish-type);
}

.nav-menu a:hover {
	/*text-decoration: underline;*/
    cursor: pointer;
    /*color: var(--blackish-type);*/
    color: #6d6d6d;
}

.nav-block {
	display: none;
}

.burger {
	display: none;
}

.langtoggle {
	text-decoration: none !important;
}

.langtoggle-off {
	color: #a8a8a8;
}

a:hover .langtoggle-off {
	color: var(--blackish-type);
}

a:hover .langtoggle-on {
	color: #a8a8a8;
}

@media (max-width: 767px)  {
	
	
	.nav {
		top: -350px;
	}

	.fr .nav-holder, .nav-holder {
		position: absolute;
		left: 0;
		max-width: 100%;
	}

	.nav-menu {
		
	    display: block;

	    position: absolute;
	    top: -280px;
	    transform: none;
	    width: 100%;
	    transition: top 0.25s;
	}

	.nav-menu.show {
		top: var(--nav-height);
		transition: top 0.25s;
	}

	.nav-menu li {
		height: var(--nav-height);
	    background-color: white;
	}

	.nav-menu li a {
		display: block;
		font-size: 24px;
		text-align: center;
		position: relative;
		top: 50%;
		transform: translateY(-50%);
	}

	.nav-menu li {
		border-top: 2px solid  var(--alt-bg-color);
		border-bottom: 2px solid  var(--alt-bg-color);
	}

	.nav-menu li:first-child {
		border-top: none;
	}
	
	.nav-menu li:last-child {
		border-bottom: none;
	}

	/*.nav-menu li:nth-child(even)  {

	    background-color: var(--alt-bg-color);
	}*/
	
	.nav-block {
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: var(--alt-bg-color);
	}

	.burger {
		display: block;
		position: absolute;
		
		top: 0;
		right: 14px;
		
		width: 40px;
		height: 100%;
	}

	.burger img {
		display: block;
		position: relative;
		top: 50%;
		transform: translateY(-50%);
	}
}


